@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap);
:root {
    --background: #1B1B1E;
    --border: #474747;
    --text: #D8DBE2;
}

@media only screen and (max-width: 560px) {
    body * {
        visibility: hidden;
    }
    body {
        background-color: green;
    }
}

* {
    font-family: 'Comfortaa', sans-serif;
}

body {
    margin: 0px;
    height: 100vh;
    /* margin-top: -5px; */
}

#root {
    height: 100%;    
}

:root {
    --sidebar-size: 22vw;
    --sidebar-padding-h: 1vh;
    --sidebar-padding-w: 1.5vw;

    --sidebar-width: calc(var(--sidebar-size) - 2 * var(--sidebar-padding-w));
    --sidebar-height: calc(100% - 2 * var(--sidebar-padding-h));

    --sidebar-hr-size: calc(var(--sidebar-size) - 1vw);
    --sidebar-hr-margin: calc(-1 * var(--sidebar-padding-w) + 0.5vw);
}

.sidebar {
    /* width: var(--sidebar-width); */
    min-width: calc(22vw - 2 * 1.5vw);
    min-width: var(--sidebar-width);
    height: calc(100% - 2 * 1vh);
    height: var(--sidebar-height);
    padding: 1vh 1.5vw;
    padding: var(--sidebar-padding-h) var(--sidebar-padding-w);
    background-color: var(--background);
    font-size: 20px;
}

hr {
    margin: 10px calc(-1 * 1.5vw + 0.5vw);
    margin: 10px var(--sidebar-hr-margin);
    width: var(--sidebar-hr);
}

span {
    color: var(--text);
}

.number-input-group input {
    text-align: center;
    border: 3px solid var(--border);
    border-radius: 8px;
    margin: 0 5px;
}

.app {
    display: flex;
    height: 100%;
}

.scene {
    max-width: 76vw;
    min-width: 50vw;
    width: 100%;
    height: 100%;
}

